* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  height: 100vh;
}

.principal,
.envoltura {
  min-height: 100%;
  min-width: 100%;
}

@media only screen and (min-width: 768px) {
  .principal {
    display: flex;
    flex-direction: column;
    justify-content: center;
    flex-grow: 1;
  }
}
body {
  font-family: "Segoe UI", Roboto, Helvetica, Arial, sans-serif;
  background-color: #000000;
}

.btn-volver {
  background-color: transparent;
  border: none;
  cursor: pointer;
}

.btn-option {
  background-color: transparent;
  border: none;
  cursor: pointer;
}

.btn-color {
  flex: 1;
  display: flex;
  justify-content: center;
  background-color: #4a5df9;
  border: none;
  padding: 10px 100px;
  border-radius: 8px;
  cursor: pointer;
}

.btn-sincolor {
  flex: 1;
  display: flex;
  justify-content: center;
  background-color: #25292e;
  border: none;
  padding: 10px 100px;
  border-radius: 8px;
  cursor: pointer;
}
.btn-sincolor:hover {
  background-color: rgb(54, 60, 68);
  transition: transform 0.2s;
}

.btn-cuenta {
  flex: 1;
  display: flex;
  justify-content: center;
  background-color: #25292e;
  border: none;
  padding: 10px;
  border-radius: 8px;
}
.btn-cuenta:hover {
  background-color: rgb(54, 60, 68);
  transition: transform 0.2s;
}

.cabecera-mobile {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  padding: 10px 20px;
  position: sticky;
  top: 0;
  z-index: 10;
  border-bottom: 1.5px solid rgb(54, 54, 54);
  background-color: #000000;
}
.cabecera-mobile .h1 {
  flex: 1;
  display: flex;
  justify-content: center;
}
.cabecera-mobile .titulo-mobile {
  color: #f5f5f5;
  font-size: 1rem;
  font-weight: bold;
  text-align: center;
  font-size: 100%;
  margin: 0;
  overflow: 0;
  padding: 0;
}

.cabecera-desktop {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  gap: 0.2rem;
  padding-top: 10px;
  padding-left: 20px;
}
.cabecera-desktop .logo-header {
  width: 77px;
  height: 77px;
  border-radius: 50%;
  border: 3px solid #fff;
  object-fit: cover;
}
.cabecera-desktop .div-des {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  gap: 1rem;
}
.cabecera-desktop .div-des .lexpi {
  display: flex;
  flex-direction: row;
  gap: 0.1rem;
}

.h3-titu {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
}
.h3-titu .titu-h3 {
  color: #f5f5f5;
  font-size: 14px;
  font-weight: normal;
  text-align: center;
  margin: 0;
  overflow: 0;
  padding-top: 0;
  padding-bottom: 10px;
  white-space: nowrap;
}

.hh3 {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 1rem;
}

.h3-deskop {
  color: #f5f5f5;
  font-size: 14px;
  font-weight: normal;
  text-align: center;
  margin: 0;
  overflow: 0;
  padding: 0;
}

.h4 {
  display: flex;
  flex-direction: row;
  align-items: start;
}
.h4 .titu-h4 {
  color: #a8a8a8;
  font-size: 14px;
  font-weight: normal;
  text-align: center;
  margin: 0;
  overflow: 0;
  padding-top: 10px;
  padding-bottom: 8px;
}

.h4-titu {
  display: inline;
  cursor: pointer;
}

.titulo-h4 {
  color: #f5f5f5;
  font-size: 14px;
  line-height: 1rem;
  font-family: "Segoe UI", Roboto, Helvetica, Arial, sans-serif;
  font-weight: 400;
}

.btn-lin {
  display: flex;
  cursor: pointer;
}
.btn-lin .h3-link {
  color: #f5f5f5;
  font-size: 14px;
  font-family: "Segoe UI", Roboto, Helvetica, Arial, sans-serif;
  font-weight: 400;
  padding-left: 0.2rem;
}

.boton-color {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: stretch;
  align-self: auto;
  position: static;
  gap: 0.5rem;
  padding: 12px 0;
}
.boton-color .span {
  color: #f5f5f5;
  font-weight: 400;
  text-align: center;
  font-size: 14px;
  font-family: "Segoe UI", Roboto, Helvetica, Arial, sans-serif;
}
.boton-color .mensaje {
  color: #f5f5f5;
  font-weight: 400;
  text-align: center;
  font-size: 14px;
  font-family: "Segoe UI", Roboto, Helvetica, Arial, sans-serif;
}

