.enla {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  gap: 8rem;
  padding-top: 20px;
}
.enla .publi {
  max-width: 193px;
}
.enla .rel {
  max-width: 193px;
}
.enla .perso {
  max-width: 193px;
}
.enla .nela-a {
  text-decoration: none;
}

@media only screen and (min-width: 768px) {
  .cabecera-mobile {
    display: none;
  }
}
.aside-pie {
  position: sticky;
  bottom: 0;
  z-index: 10;
  border-top: 1.5px solid rgb(54, 54, 54);
  background-color: #000000;
}
.aside-pie .div-pie {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  gap: 1rem;
  padding: 10px 20px;
}

.barra-lateral {
  display: none;
}

@media only screen and (min-width: 768px) {
  .aside-pie {
    display: none;
  }
  .barra-lateral {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    align-items: flex-end;
  }
}
.principal {
  padding: 0 0;
}

.flex-row {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  gap: 1rem;
}

.hh3 {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 1rem;
  max-width: 50%;
  margin-top: 0.75rem;
}

.div-logo {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  gap: 3rem;
  margin: 1rem 1rem 0 0;
}
.div-logo .logo-header {
  width: 77px;
  height: 77px;
  border-radius: 50%;
  border: 3px solid #fff;
  object-fit: cover;
}
.div-logo .titulo-d {
  color: #f5f5f5;
  font-size: 24px;
  font-weight: bold;
  text-align: center;
  margin-bottom: 0;
}
.div-logo .titu-h3 {
  display: none;
}

@media only screen and (max-width: 768px) {
  .titu-h3 {
    color: #f5f5f5;
    font-size: 14px;
    font-weight: normal;
    text-align: center;
    margin: 0;
    overflow: 0;
    padding-top: 0;
    padding-bottom: 10px;
    white-space: nowrap;
  }
}
.seccion-principal {
  display: flex;
  width: 100%;
  height: 100%;
}
.seccion-principal .imagen-seccion {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: repeat(1, 1fr);
  gap: 0.1rem;
  width: 100%;
  height: 100%;
}
.seccion-principal .seccion-imagen {
  width: 200px;
  height: 200px;
}